var React = require('react'); 
var ReactDOM = require('react-dom'); 

        //    drag and drop  ReactJS   : 
        //  onDrag -         
        //  onDragEnd -     
        //  onDragEnter -      
        //  onDragLeave -         
        //  onDragOver -         
        //  onDragStart -         
        //  onDrop -   drop  
        
        var Demo = React.createClass({

            dragStartHandler: function(e) {
                e.target.style.backgroundColor = 'blue';
                e.dataTransfer.effectAllowed = 'move';
                e.dataTransfer.setData('text/plain', e.target.id); 
                return true;
            }, 
            
            dragOverHandler: function(e) { 
      
                e.preventDefault();   
            }, 

            dropHandler: function(e) {
                
                var data = e.dataTransfer.getData("text/plain");
                e.target.appendChild(document.getElementById(data)); 
               
                console.log('drop!'); 
            }, 

            render: function() {
                var bricks = []; 
                for (var i = 0; i < 10; i++) {
                    bricks.push(<div key={i} id={i} onDragStart={this.dragStartHandler} className="brick" draggable="true" ></div>);
                } 

                return (
                <div>
                    <div>{bricks}</div>
                    <div className="container" onDragOver={this.dragOverHandler} onDrop={this.dropHandler}><h2>Drag bricks here</h2></div>
                </div> 
            )}
        })

        var container = document.getElementById('example');
        ReactDOM.render(<Demo />, container);


